import React from 'react';
import { Box, } from '@material-ui/core';
import { makeStyles, useTheme } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    barSet: {
        padding: '8px 0',
        width: '100%',
        fontWeight: 'normal',
        fontStyle: 'normal',
        fontSize: '10pt',
        boxSizing: 'border-box',
        display: 'inline-block',
        lineHeight: '10pt',
        [theme.breakpoints.down('sm')]: {
            maxWidth: theme.breakpoints.width('sm'),
        },
    },
    barRow: {
        boxSizing: 'border-box',
        display: 'block',
        padding: '3px 0',
        minHeight: '32px',
        clear: 'both',
    },
    barLabel: {
        boxSizing: 'border-box',
        paddingRight: '15px',
        marginTop: '3px',
        width: '44%',
        display: 'inline-block',
        float: 'left',
        textAlign: 'right',
        verticalAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            textAlign: 'left',
            marginBottom: '2px',
            display: 'block',
            float: 'none',
            width: 'auto',
        },
    },

    barLabel2: {
        fontWeight: 'bolder',
        width: '6%',
        minWidth: '45px',
        paddingRight: '10px',
        textAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            textAlign: 'left',
            marginBottom: '2px',
            display: 'block',
            float: 'none',
            width: 'auto',
        },
    },
    list: {
        listStyle: 'none',
        margin: '0',
        padding: '0',
        display: 'inline-block',
        position: 'relative',
        boxSizing: 'border-box',
        // paddingInlineStart: '10px',
        width: '40%',
        [theme.breakpoints.down('sm')]: {
            width: '100%',
        },
    },
    bar: {
        border: '2px solid #000',
        marginTop: '2px',
        height: '14px',
        content: '',
        lineHeight: '16px',
        textAlign: 'right',
        paddingTop: '1px',
        color: theme.palette.primary.main,
        backgroundColor: theme.palette.primary.main, 
        borderColor:  theme.palette.primary.main 
    }
}));

const HorizontalBarChartRespWidth = ({ data, color }) => {
    const classes = useStyles();
    const theme = useTheme();
    const max = Math.max(...data.map(row => row.value))

    return (
        <Box className={classes.barSet} style={{ color: color }}>
            {data.sort((a, b) => b.value - a.value)
                .map((row, i) =>
                    <Box key={`chart-bar-${i}`} className={classes.barRow}>
                        <Box className={classes.barLabel}>{row.category}</Box>
                        <Box className={`${classes.barLabel} ${classes.barLabel2}`}>{row.value}</Box>
                        <ul className={classes.list}>
                            <li style={{ width: `${row.value / max * 100}%` }} 
                                className={classes.bar} />
                        </ul>
                    </Box>

                )}
        </Box>
    )
};

export default HorizontalBarChartRespWidth;
